import { MODEL_SIGNATURES_MANIFEST } from '@porsche-design-system/assets';
import { getCDNBaseURL } from '../../utils';
export const MODEL_SIGNATURE_FETCH_PRIORITY = ['low', 'high', 'auto'];
export const MODEL_SIGNATURE_SIZES = ['small', 'inherit'];
export const MODEL_SIGNATURE_MODELS = Object.keys(MODEL_SIGNATURES_MANIFEST);
export const MODEL_SIGNATURE_COLORS = [
    'primary',
    'contrast-low',
    'contrast-medium',
    'contrast-high',
    'inherit',
];
export const getSvgUrl = (model) => {
    return `${getCDNBaseURL()}/model-signatures/${MODEL_SIGNATURES_MANIFEST[model].src}`;
};
